import * as React from "react";
import { Component } from "react";

interface Props {
  toShow: boolean;
}

interface State {
  showReady: boolean;
  showGo: boolean;
}

class FightStart extends Component<Props, State> {
  state = {
    showReady: false,
    showGo: false
  };

  componentWillReceiveProps(nextProps: Props) {
    if (nextProps.toShow !== this.props.toShow) {
      this.showAnimation();
    }
  }

  showAnimation = () => {
    this.setState(
      prevState => ({
        ...prevState,
        showReady: true
      }),
      () => {
        setTimeout(() => {
          this.setState(prevState => ({
            ...prevState,
            showReady: false,
            showGo: true
          }));
        }, 1000);
      }
    );
  };

  render() {
    return (
      <div className={`FightStart ${this.props.toShow ? "show" : ""}`}>
        <h1 className={`ready ${this.state.showReady ? "show" : ""}`}>
          מוכנים?
        </h1>
        <h1 className={`go ${this.state.showGo ? "show" : ""}`}>הלחמו!</h1>
        <style jsx>{`
          .FightStart {
            position: absolute;
            direction: rtl;
            margin: auto;
            height: 15rem;
            width: 70%;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 5;
          }

          .FightStart.show {
            display: block;
          }

          .FightStart h1 {
            position: absolute;
            text-align: center;
            font-size: 10rem;
            color: #c00;
            transform: scale(0);
            transition: all 0.3s;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
          }

          .FightStart h1.show {
            transform: scale(1);
          }
        `}</style>
      </div>
    );
  }
}

export default FightStart;
